.BlogRoot {
    background-color: #fff;
    overflow: hidden;
    white-space: nowrap;
    box-shadow: 0px 0px 30px rgba(0,0,0,0.2);
}

.BlogImage {
    width: 22vw;
    height: 60vh;
    display: inline-block;
    background-image: url(images/wynk_blog.png);
    background-position: right 30%;
    background-repeat: no-repeat;
    background-size: contain;
    vertical-align: top;
    margin-left: 8vw;
}

.BlogInner {
    width: 75vw;
    display: inline-block;
    vertical-align: top;
    transform: scale(0.85);
}

.BlogInnerTitle {
    line-height: 8vh;
    font-size: 2.5vw;
    font-weight: 700;
    padding-top: 30px;
    color: #212121;
    padding-left: 40px;
	font-family: 'GothamRounded Bold';
}

.BlogItem {
    width: 11vw;
    height: 26vh;
    margin: 3vh 0vh 0vh 3vh;
    padding: 2.5vh 2.5vh 4vh 2.5vh;
    display: inline-block;
    background-color: rgba(0,0,0,0.1);
    border-radius: 0.6vw;
    transform: scale(0.95);
    cursor: pointer;
    transition: transform 0.2s;
    background-color: #e73e2f;
    vertical-align: top;
}

.BlogItem:hover {
    transform: scale(1);
}

.BlogItemTitle {
    font-size: 1.1vw;
    font-weight: bold;
    color: #FFF;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
	font-family: 'GothamRounded Bold';
}

.BlogItemText {
    text-align: justify;
    font-size: 0.8vw;
    white-space: normal;
    padding-top: 1.5vh;
    height: 19vh;
    overflow: hidden;
    text-overflow: ellipsis;
    color: rgba(255,255,255,0.6);
    line-height: 2.2vh;
    margin-bottom: 1vh;
}

.BlogItemAuth {
    width: 7vh;
    height: 7vh;
    background-color: #FFF;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 0.7vw;
    border: 0.2vw solid #FFF;
    display: inline-block;
}

.BlogItemAuthName {
    padding-left: 0.7vw;
    display: inline-block;
    font-size: 0.7vw;
    color: #FFF;
    vertical-align: top;
    font-weight: 600;
    padding-top: 0.7vh;
}

.BlogButton {
    display: none;
    text-align: center;
    line-height: 50px;
    width: 180px;
    margin: 5px auto 30px auto;
    background-color: #4d95ff;
	font-family: 'GothamRounded Bold';
    color: #FFF;
    border-radius: 10px;
    font-size: 14px;
    cursor: pointer;
}

.BlogItemMore {
    background-color: rgba(0,0,0,0.2);
    background-image: url("data:image/svg+xml,<svg id='svg' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' style='transform: rotate(0deg); fill: rgba(41, 41, 41, 0.65);'><path fill-rule='evenodd' d='M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z'></path></svg>");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 12vh;
}

.BlogItemTitleMore {
    color: rgba(0,0,0,0.7);
    text-align: center;
    line-height: 7vh;
}

@media (orientation: portrait) {
    .BlogInner, .BlogItemMore {
        display: none;
    }
    .BlogImage {
        width: auto;
        height: 35vh;
        display: block;
        background-position: center;
        background-size: auto 95%;
        margin-left: 0px;
    }
    .BlogButton {
        display: block;
    }
}