.PortfolioRoot {
    height: calc(100vh - 60px);
    padding-top: 60px;
    background-color: rgb(221, 220, 219);
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: left;
}

.PortfolioTitle {
    line-height: 30px;
    font-size: 2.6vw;
    font-weight: 700;
    padding-top: 10vh;
    color: #212121;
    padding-left: calc(10vw);
	font-family: 'GothamRounded Bold';
}

.PortfolioInner {
    margin: 0px auto;
    height: calc(100vh - 170px);
    transform: scale(0.85);
}

.PortfolioInfo {
    width: calc(39vw - 120px);
    height: inherit;
    display: inline-block;
    vertical-align: top;
}

.PortfolioPlay {
    width: 48vw;
    height: calc(100vh - 330px);
    display: inline-block;
    vertical-align: top;
    margin: 0px 1.5vw;
}

.PortfolioPlay > video {
    width: 45vw;
    position: absolute;
    box-shadow: 0px 20px 80px rgba(0,0,0,0.6);
    transition-duration: 0.3s;
    transition-property: opacity, transform;
    margin-top: calc(50vh - 14vw - 70px);
    cursor: pointer;
}

.PortfolioButton {
    width: 60px;
    height: calc(100vh - 330px);
    margin-top: 8vh;
    border: none;
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 70px auto;
    cursor: pointer;
    opacity: 0.65;
    transition: opacity 0.3s;
    vertical-align: middle;
    z-index: 20;
}

.PortfolioButton:hover {
    opacity: 1;
}

.PortfolioButtonBack {
    background-image: url("data:image/svg+xml,<svg id='svg' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' style='transform: rotate(0deg); fill: rgba(17, 67, 146, 1);'><path fill-rule='evenodd' d='M9.224 1.553a.5.5 0 0 1 .223.67L6.56 8l2.888 5.776a.5.5 0 1 1-.894.448l-3-6a.5.5 0 0 1 0-.448l3-6a.5.5 0 0 1 .67-.223z'></path></svg>");
    margin-left: 60px;
}

.PortfolioButtonNext {
    background-image: url("data:image/svg+xml,<svg id='svg' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' style='transform: rotate(0deg); fill: rgba(17, 67, 146, 1);'><path fill-rule='evenodd' d='M6.776 1.553a.5.5 0 0 1 .671.223l3 6a.5.5 0 0 1 0 .448l-3 6a.5.5 0 1 1-.894-.448L9.44 8 6.553 2.224a.5.5 0 0 1 .223-.671z'></path></svg>");
}

.PortfolioName {
    font-size: 2vw;
    margin-top: 6vh;
    margin-left: 60px;
    margin-bottom: 10px;
    color: #114392;
    opacity: 0.85;
}

.PortfolioText {
    padding-top: 15px;
    margin-left: 60px;
    font-size: 1.1vw;
    max-height: calc(100vh - 400px);
    overflow-y: hidden;
}

.PortfolioDemoButton {
    background-color: transparent;
    color: #114392;
    border: 2px solid #114392;
    margin-left: 60px;
    margin-top: 35px;
    padding: 12px 20px;
    opacity: 0.65;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s;
}

.PortfolioDemoButton:hover {
    opacity: 0.85;
    background-color: #11439222;
}

.PortfolioCallButton {
    background-color: transparent;
    color: #114392;
    border: 2px solid #114392;
    margin-left: 60px;
    margin-top: 15px;
    padding: 12px 20px;
    opacity: 0.65;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s;
}

.PortfolioCallButton:hover {
    opacity: 0.85;
    background-color: #11439222;
}

@media (min-width: 1200px) {
    .PortfolioCallButton {
        margin-left: 15px;
    }
}

@media (orientation: portrait) {
    .PortfolioPlay {
        display: none;
    }
    .PortfolioButton { 
        display: none;
    }
    .PortfolioRoot {
        height: auto;
        padding-top: 0px;
        background-color: #FFF;
    }
    .PortfolioInner {
        height: auto;
    }
    .PortfolioTitle {
        font-size: 2.6vw;
        font-weight: 700;
        padding: 0px 0px 20px 0px;
        text-align: center;
        font-size: 20px;
        font-weight: 700;
        text-transform: uppercase;
        color: #212121;
    }
    .PortfolioInner {
        margin: 0px;
        width: auto;
        transform: scale(1);
    }
    .PortfolioItem {
        width: auto;
        display: block;
        height: 100px;
        margin: 0px;
        background-position: center;
        background-size: 100% auto;
        filter: grayscale(60%);
        opacity: 0.8;
    
    }
    .PortfolioItemTitle {
        font-size: 20px;
        line-height: 20px;
        line-height: 100px;
        background-color: rgba(0,0,0,0.8);
        text-align: center;
        color: #FFF;
    }

}